body {
  margin: 0;
}
.header-fixed {
    position: fixed;
    top: 0;
    z-index: 1;
    width: 100%;
    background-color: rgba(21, 21, 21, 1);
    box-shadow: 1px 1px 4px 1px rgba(0,0,0,0.1);
}

.navbar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    color: #fff;
    line-height: 60px;
}

.navbar .logo {
    -webkit-flex: 3 1;
            flex: 3 1; 
}

.navbar .logo a {
    display: block;
    font-size: 30px;
    font-weight: bold;
    color: #fff;
    text-decoration: none;
}
.navbar .logo a:hover {
  color: #777777;
}

.navbar nav {
    -webkit-flex: 8 1;
            flex: 8 1;
}

.navbar label {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  padding: 28px 20px;
  position: relative;
  z-index: 3;
}

.navbar label i {
  height: 2px;
  position: relative;
  -webkit-transition: background .2s ease-out;
  transition: background .2s ease-out;
  width: 18px;
  font-style: normal;
  font-weight: normal;
}
.navbar label i:before,
.navbar label i:after {
  content: '';
  height: 100%;
  position: absolute;
  -webkit-transition: all .2s ease-out;
  transition: all .2s ease-out;
  width: 100%;
}
.navbar label i,
.navbar label i:before,
.navbar label i:after {
  display: block;
  background: #eee;  
}
.navbar label i:before {
  top: 5px;
}
.navbar label i:after {
  top: -5px;
}

.navbar #navbar-toggle {
  display: none;
}

.header #navbar-toggle:checked ~ .menu {
  visibility: visible;
  opacity: 0.99;
}
.header #navbar-toggle:checked ~ label {
  background: #212121;
  border-radius: 50%;    
}
.header #navbar-toggle:checked ~ label i {
  background: transparent;
}
.header #navbar-toggle:checked ~ label i:before {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.header #navbar-toggle:checked ~ label i:after {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.header #navbar-toggle:checked ~ label:not(.steps) i:before,
.header #navbar-toggle:checked ~ label:not(.steps) i:after {
  top: 0;
}

@media (max-width: 768px) {
    .navbar nav {
      visibility: hidden;
      opacity: 0;
      z-index: 2;
      position: fixed;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
      -webkit-transition: all 0.3s ease-out;
      transition: all 0.3s ease-out;
      display: table;
      background: #ddd;
    }
    .navbar nav ul {
      margin: 0;
      padding: 20px 0;
      display: table-cell;
      vertical-align: middle;
    }
    .navbar nav li {
      display: block;
      text-align: center;
      padding: 20px 0;
      text-align: center;
      font-size: 50px;
      min-height: 50px;
      font-weight: bold;
      cursor: pointer;
      -webkit-transition: all 0.3s ease-out;
      transition: all 0.3s ease-out;
    }
    .navbar nav li:hover {
      background: #212121;
    }
    .navbar nav li:hover a {
      color: #fff;
      -webkit-transition: all 0.3s ease-out;
      transition: all 0.3s ease-out;
    }
    .navbar nav li a {
      color: #212121;
    }
}

@media (min-width: 768px) {
    .navbar nav ul {
      margin: 0;
      padding: 0;
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: space-around;
              justify-content: space-around;
      text-align: center;
      list-style: none;
    }
    .navbar nav li {
      -webkit-flex: 1 1;
              flex: 1 1;
    }
    .navbar nav li a {
      display: block;
      padding: 0 8px;
      font-size: 16px;
      line-height: 60px;
      color: #fff;
      text-decoration: none;
    }
    .navbar nav li.active {
      background: #555;  
    }
    .navbar nav li:hover {
      background: #333;
    }
    .navbar label {
      display: none;
    }
}


